body {
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  flex-basis: 100%;
  flex-grow: 1;
  flex-shrink: 0;
  text-align: center;

  font-family: Arial, sans-serif;
  font-size: 100px;
  line-height: 3em;
  color: #fff;
}

.block--1 {
  background-color: #f00;
}

.block--2 {
  background-color: #c00;
}

.block--3 {
  background-color: #900;
}

.block--4 {
  background-color: #600;
}

.block--5 {
  background-color: #300;
}

.block--6 {
  background-color: #000;
}

@media (min-width: 600px) {
  .block {
    flex-basis: 50%;
  }

  .block--3 {
    order: 4;
  }

  .block--4 {
    order: 3;
  }

  .block--5,
  .block--6 {
    order: 5;
  }
}

@media (min-width: 900px) {
  .block {
    flex-basis: 33.3%;
  }

  .block--3 {
    order: 3;
  }

  .block--4 {
    order: 6;
  }

  .block--5 {
    order: 5;
  }

  .block--6 {
    order: 4;
  }
}
